export const CHARACTER_ORDER = [
  'frieren',
  'himmel',
  'heiter',
  'eisen',
  'fern',
  'stark',
  'sein',
  'bel'
] as const;

export type CharacterId = typeof CHARACTER_ORDER[number];

export const CHARACTER_COLORS: Record<CharacterId, string> = {
  frieren: '#7f7c84',
  himmel: '#bddaf9',
  heiter: '#78855e',
  eisen: '#cfccc0',
  fern: '#794983',
  stark: '#af4a33',
  sein: '#936f42',
  'bel': '#667240'
};

export function nextUnassignedCharacter(used: Set<string>): CharacterId | null {
  for (const c of CHARACTER_ORDER) {
    if (!used.has(c)) return c;
  }
  return null;
}

export function spriteUrlFor(character: CharacterId): string {
  // Relative path leverages Vite dev proxy and absolute static in prod
  return `/media/sprites/square_${character}.png`;
}
