import React, { useEffect, useRef, useState } from 'react';
import { useAppStore } from '../lib/store';
import { isVerbose, getSocket } from '../lib/socket';

interface Fragment { start_ms: number; end_ms: number; start_byte: number; end_byte: number; }
interface IndexJson { duration_ms: number; fragments: Fragment[]; }

interface Props { mediaUrl: string; indexUrl: string; mediaId: string; }

// Minimal fMP4 append (single file) with basic first-chunk readiness emit.
export default function MediaPlayer({ mediaUrl, indexUrl, mediaId }: Props) {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [mseReady, setMseReady] = useState(false);
  const [fragments, setFragments] = useState<Fragment[]>([]);
  const [durationMs, setDurationMs] = useState<number | null>(null);
  const readiness = useAppStore(s => s.readiness);
  const snapshot = useAppStore(s => s.snapshot);
  const emittedRef = useRef(false);
  const sourceBufferRef = useRef<SourceBuffer | null>(null);
  const mediaSourceRef = useRef<MediaSource | null>(null);
  const pendingFetchRef = useRef<AbortController | null>(null);

  // Fetch index.json
  useEffect(() => {
    let aborted = false;
    (async () => {
      try {
        const res = await fetch(indexUrl, { cache: 'no-store' });
        if (!res.ok) return;
        const data: IndexJson = await res.json();
        if (aborted) return;
        setFragments(data.fragments || []);
        setDurationMs(data.duration_ms || null);
        if (isVerbose()) console.debug('[wp] index_loaded', { count: data.fragments?.length, duration: data.duration_ms });
      } catch (e) {
        if (isVerbose()) console.debug('[wp] index_error', (e as any)?.message);
      }
    })();
    return () => { aborted = true; };
  }, [indexUrl]);

  // Initialize MSE and append first fragment (init + first)
  useEffect(() => {
    let objectUrl: string | null = null;
    const ms = new MediaSource();
    mediaSourceRef.current = ms;
    if (videoRef.current) {
      objectUrl = URL.createObjectURL(ms);
      videoRef.current.src = objectUrl;
    }
    const onOpen = () => {
      try {
        const sb = ms.addSourceBuffer('video/mp4; codecs="avc1.4d401e, mp4a.40.2"');
        sourceBufferRef.current = sb;
        sb.addEventListener('error', () => { if (isVerbose()) console.debug('[wp] sb_error'); });
        if (fragments.length) {
          fetchAndAppendInitial();
        }
      } catch (e) {
        if (isVerbose()) console.debug('[wp] mse_source_error', (e as any)?.message);
      }
    };
    ms.addEventListener('sourceopen', onOpen);
    return () => {
      ms.removeEventListener('sourceopen', onOpen);
      if (objectUrl) URL.revokeObjectURL(objectUrl);
      if (pendingFetchRef.current) pendingFetchRef.current.abort();
    };
  }, [mediaUrl, mediaId]);

  // When fragments arrive after MSE open, attempt initial append
  useEffect(() => {
    if (mediaSourceRef.current?.readyState === 'open' && sourceBufferRef.current && fragments.length && !emittedRef.current) {
      fetchAndAppendInitial();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fragments]);

  function fetchAndAppendInitial() {
    if (!fragments.length || !sourceBufferRef.current) return;
    const first = fragments[0];
    const rangeHeader = `bytes=${first.start_byte}-${first.end_byte}`;
    const ac = new AbortController();
    pendingFetchRef.current = ac;
    fetch(mediaUrl, { headers: { Range: rangeHeader }, signal: ac.signal }).then(r => r.arrayBuffer()).then(buf => {
      if (!sourceBufferRef.current) return;
      sourceBufferRef.current.addEventListener('updateend', handleInitialAppended, { once: true });
      sourceBufferRef.current.appendBuffer(buf);
    }).catch(e => { if (isVerbose()) console.debug('[wp] init_fetch_error', (e as any)?.message); });
  }

  function handleInitialAppended() {
    if (!emittedRef.current) {
      emittedRef.current = true;
      getSocket().emit('client_ready', { media_id: mediaId, first_appended: true });
      if (isVerbose()) console.debug('[wp] client_ready emitted', { mediaId });
    }
    setMseReady(true);
  }

  // Sync play/pause & basic drift correction
  useEffect(() => {
    const v = videoRef.current; if (!v) return;
    if (snapshot.playing) {
      if (v.paused) v.play().catch(()=>{});
    } else {
      if (!v.paused) v.pause();
    }
    // Drift: compare video currentTime vs snapshot playhead
    const targetMs = snapshot.playheadMs;
    if (typeof targetMs === 'number' && mseReady) {
      const currentMs = v.currentTime * 1000;
      const drift = targetMs - currentMs;
      const abs = Math.abs(drift);
      if (abs > 500) {
        v.currentTime = targetMs / 1000; // hard snap
        if (isVerbose()) console.debug('[wp] drift_snap', { drift });
      } else if (abs > 80) {
        // small nudge by setting currentTime (simpler than rate tweak for MVP)
        v.currentTime = targetMs / 1000;
        if (isVerbose()) console.debug('[wp] drift_nudge', { drift });
      }
    }
  }, [snapshot.playing, snapshot.playheadMs, mseReady]);

  return (
    <div className="relative w-full h-full bg-black">
      <video ref={videoRef} className="w-full h-full" playsInline muted />
      {!readiness.ready && (
        <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
          <div className="px-4 py-2 rounded bg-slate-900/80 text-xs font-medium">
            Syncing ({readiness.readyCount}/{readiness.total})
          </div>
        </div>
      )}
      {durationMs && (
        <div className="absolute bottom-2 right-2 text-[10px] opacity-60 bg-black/40 px-2 py-1 rounded">
          {(durationMs/1000).toFixed(0)}s
        </div>
      )}
    </div>
  );
}
