import { fetchCatalog } from './catalog';
import { isVerbose } from './socket';
import { getRuntimeConfig } from './config';

// Developer utility (verbose mode) to sanity check media catalog + sample media accessibility.
// Invoked when MediaDrawer opens in verbose mode.
export async function probeMediaEndpoints() {
  if (!isVerbose()) return;
  try {
    const categories = ['output/fmp4/anime','output/fmp4/movie','output/fmp4/tv'];
    for (const cat of categories) {
      const catData = await fetchCatalog(cat);
      console.debug('[wp][probe] catalog', cat, { dirs: catData.dirs.length, files: catData.files.length });
      // Try first playable leaf if present
      const candidate = catData.files[0];
      if (candidate) {
        const base = mediaBase();
        const indexUrl = `${base}/${candidate.id}/index.json`.replace(/\\/g,'/');
        const mediaUrl = `${base}/${candidate.id}/output_frag.mp4`.replace(/\\/g,'/');
        await fetchAndLog(indexUrl, 'index');
        // Range fetch first ~1KB
        await fetchAndLog(mediaUrl, 'fragment', { headers: { Range: 'bytes=0-1023' } });
      }
    }
  } catch (err: any) {
    console.debug('[wp][probe] error', err?.message || String(err));
  }
}

function mediaBase() {
  const config = getRuntimeConfig();
  return config.mediaBase;
}

async function fetchAndLog(url: string, label: string, init?: RequestInit) {
  const t0 = performance.now();
  const res = await fetch(url, init);
  const ms = Math.round(performance.now() - t0);
  console.debug('[wp][probe]', label, { url, status: res.status, ms, bytes: res.headers.get('content-length') });
  return res;
}
