import { create } from 'zustand';
import { CHARACTER_ORDER } from './characters';

// --- Phase 2 types ---
export interface RosterUser {
  clientId: string;
  name: string;
  sprite: string | null;
  isLeader?: boolean;
}

export interface SnapshotState {
  mediaId: string | null;
  playheadMs: number;
  playing: boolean;
  serverSeq: number; // last authoritative seq applied
  leaderId: string | null;
}

export interface MeState {
  clientId: string | null;
  nextClientSeq: number; // local incrementing control seq (client scoped)
}

export interface DriftState {
  rtts: number[]; // last 5 RTT samples (ms)
  offsetMs: number; // estimated serverTime - clientTime
}

export interface ChatMessage {
  id: string; // could be server seq or timestamp derived
  clientId: string;
  name: string;
  sprite: string | null;
  text: string;
  ts: number; // epoch ms
}

interface AppState {
  // existing identity/admin
  ready: boolean;
  name: string;
  sprite: string | null;
  adminKey: string | null;
  rememberAdmin: boolean;
  // networking + protocol state
  me: MeState;
  snapshot: SnapshotState;
  roster: Record<string, RosterUser>; // keyed by clientId
  chat: ChatMessage[];
  drift: DriftState;
  readiness: { mediaId: string | null; ready: boolean; readyCount: number; total: number };
  // maps client_seq -> server_seq (populated on ack)
  seqMap: Record<number, number>;
  toasts: { id: string; kind: 'error' | 'info'; msg: string; ts: number }[];
  // actions
  setReady: () => void;
  setIdentity: (name: string, sprite: string | null) => void;
  ensureAutoIdentity: () => void;
  setAdminKey: (key: string | null, remember: boolean) => void;
  hydrateAdminKey: () => void;
  ensureMe: (clientId: string) => void;
  applySnapshot: (snap: Partial<SnapshotState> & { users?: RosterUser[] }) => void;
  upsertUser: (u: RosterUser) => void;
  removeUser: (clientId: string) => void;
  appendChat: (m: ChatMessage) => void;
  recordAck: (clientSeq: number, serverSeq: number) => void;
  allocateClientSeq: () => number;
  updateControlState: (delta: { playheadMs?: number; playing?: boolean; serverSeq?: number; leaderId?: string }) => void;
  addRttSample: (rtt: number, serverTime?: number, clientSendTs?: number) => void;
  pushToast: (kind: 'error' | 'info', msg: string) => void;
  popToast: (id: string) => void;
  setReadiness: (r: { mediaId: string; ready: boolean; readyCount: number; total: number }) => void;
}

const ADMIN_KEY_STORAGE = 'wp_leader_key';

export const useAppStore = create<AppState>((set, get) => ({
  ready: false,
  name: '',
  sprite: null,
  adminKey: null,
  rememberAdmin: false,
  me: { clientId: null, nextClientSeq: 1 },
  snapshot: { mediaId: null, playheadMs: 0, playing: false, serverSeq: 0, leaderId: null },
  roster: {},
  chat: [],
  drift: { rtts: [], offsetMs: 0 },
  readiness: { mediaId: null, ready: false, readyCount: 0, total: 0 },
  seqMap: {},
  toasts: [],
  setReady: () => set({ ready: true }),
  setIdentity: (name, sprite) => set({ name, sprite }),
  ensureAutoIdentity: () => set((s) => {
    if (s.name) return {};
    const used = new Set(Object.values(s.roster).map(r => r.name).filter(Boolean));
    for (const c of CHARACTER_ORDER) {
      if (!used.has(c)) {
        return { name: c, sprite: c };
      }
    }
    // If all characters taken, keep anonymous but do not force 'Guest'
    return { name: CHARACTER_ORDER[0], sprite: CHARACTER_ORDER[0] };
  }),
  setAdminKey: (key, remember) => {
    if (remember && key) localStorage.setItem(ADMIN_KEY_STORAGE, key);
    else localStorage.removeItem(ADMIN_KEY_STORAGE);
    set({ adminKey: key, rememberAdmin: remember });
  },
  hydrateAdminKey: () => {
    const stored = localStorage.getItem(ADMIN_KEY_STORAGE);
    if (stored) set({ adminKey: stored, rememberAdmin: true });
  },
  ensureMe: (clientId) => set((s) => ({ me: { ...s.me, clientId } })),
  applySnapshot: (snap) => set((s) => {
    const rosterUpdates: Record<string, RosterUser> = { ...s.roster };
    if (snap.users) {
      for (const u of snap.users) rosterUpdates[u.clientId] = u;
    }
    return {
      snapshot: {
        mediaId: snap.mediaId ?? s.snapshot.mediaId,
        playheadMs: snap.playheadMs ?? s.snapshot.playheadMs,
        playing: snap.playing ?? s.snapshot.playing,
        serverSeq: snap.serverSeq ?? s.snapshot.serverSeq,
        leaderId: snap.leaderId ?? s.snapshot.leaderId
      },
      roster: rosterUpdates
    };
  }),
  upsertUser: (u) => set((s) => ({ roster: { ...s.roster, [u.clientId]: u } })),
  removeUser: (clientId) => set((s) => {
    const copy = { ...s.roster }; delete copy[clientId]; return { roster: copy };
  }),
  appendChat: (m) => set((s) => ({ chat: [...s.chat, m].slice(-500) })),
  recordAck: (clientSeq, serverSeq) => set((s) => ({ seqMap: { ...s.seqMap, [clientSeq]: serverSeq } })),
  allocateClientSeq: () => {
    const n = get().me.nextClientSeq;
    set((s) => ({ me: { ...s.me, nextClientSeq: n + 1 } }));
    return n;
  },
  updateControlState: (delta) => set((s) => ({
    snapshot: { ...s.snapshot, ...delta }
  })),
  addRttSample: (rtt, serverTime, clientSendTs) => set((s) => {
    const rtts = [...s.drift.rtts, rtt].slice(-5);
    let offsetMs = s.drift.offsetMs;
    if (serverTime && clientSendTs) {
      const clientReceive = Date.now();
      const clientMid = clientSendTs + rtt / 2;
      offsetMs = serverTime - clientMid;
    }
    return { drift: { rtts, offsetMs } };
  })
  ,
  pushToast: (kind, msg) => set((s) => ({ toasts: [...s.toasts, { id: Math.random().toString(36).slice(2), kind, msg, ts: Date.now() }].slice(-5) })),
  popToast: (id) => set((s) => ({ toasts: s.toasts.filter(t => t.id !== id) }))
  ,setReadiness: (r) => set({ readiness: { mediaId: r.mediaId, ready: r.ready, readyCount: r.readyCount, total: r.total } })
}));
