import { getSocket } from './socket';
import { useAppStore } from './store';

// Unified control sender ensuring sequential client_seq for EVERY control (including load/home)
export function sendControl(kind: string, extra?: Record<string, any>) {
  const st = useAppStore.getState();
  const client_seq = st.allocateClientSeq();
  const payload = { kind, client_seq, ...(extra || {}) };
  getSocket().emit('control', payload);
  return client_seq;
}

export function sendLoad(mediaId: string) {
  return sendControl('load', { media_id: mediaId });
}

export function sendHome() {
  return sendControl('home');
}
