import React from 'react';
import { createRoot, type Root } from 'react-dom/client';
import { RouterProvider } from 'react-router-dom';
import type { AppProps } from './App';
import { createWatchpartyRouter } from './router';
import { setRuntimeConfig } from './lib/config';
import './styles.css';

export type { AppProps } from './App';
export { createWatchpartyRouter } from './router';
export type { CreateWatchpartyRouterOptions } from './router';

export type WatchpartyMountProps = AppProps & {
  basename?: string;
  strictMode?: boolean;
};

type MountedInstance = {
  root: Root;
};

function render(el: HTMLElement, routerContent: React.ReactElement, strictMode: boolean) {
  const root = createRoot(el);
  const element = strictMode ? (
    <React.StrictMode>{routerContent}</React.StrictMode>
  ) : (
    routerContent
  );
  root.render(element);
  return root;
}

export function mount(el: HTMLElement, props: WatchpartyMountProps = {}) {
  const { basename, strictMode = true, ...appProps } = props;
  
  // Set runtime config for non-component code (socket, catalog, etc.)
  setRuntimeConfig({
    serverOrigin: appProps.serverOrigin,
    defaultRoomKey: appProps.defaultRoomKey,
    mediaBase: appProps.mediaBase,
  });
  
  const existing = (el as any).__watchparty as MountedInstance | undefined;
  existing?.root.unmount();
  const router = createWatchpartyRouter({
    basename,
    appProps: appProps as AppProps,
  });
  const root = render(el, <RouterProvider router={router} />, strictMode);
  const instance: MountedInstance = { root };
  (el as any).__watchparty = instance;
  return router;
}

export function unmount(el: HTMLElement) {
  const instance = (el as any).__watchparty as MountedInstance | undefined;
  instance?.root.unmount();
  if (instance) {
    delete (el as any).__watchparty;
  }
}
