import type { RouteObject } from 'react-router-dom';
import { createBrowserRouter } from 'react-router-dom';
import App, { type AppProps } from './App';

export interface CreateWatchpartyRouterOptions {
  basename?: string;
  appProps?: AppProps;
}

export function createWatchpartyRouter(
  { basename = '/watchparty', appProps }: CreateWatchpartyRouterOptions = {},
) {
  const resolvedAppProps = appProps ?? ({} as AppProps);
  const routes: RouteObject[] = [
    { path: '/', element: <App {...resolvedAppProps} /> },
    { path: '/room/:roomKey', element: <App {...resolvedAppProps} /> },
  ];

  return createBrowserRouter(routes, { basename });
}
