import React, { useEffect, useState } from 'react';
import { Link, Outlet, useLocation, useNavigate, useParams } from 'react-router-dom';
import Toasts from './components/Toasts';
import { CHARACTER_ORDER, spriteUrlFor } from './lib/characters';
import Home from './routes/Home';
import Room from './routes/Room';
import APIDownFallback from './components/APIDownFallback';
import { ConfigContext, DEFAULT_CONFIG, type WatchpartyConfig } from './lib/config';

function RouteBody() {
  const loc = useLocation();
  const nav = useNavigate();
  const params = useParams();
  // Query param based roomKey redirect
  useEffect(() => {
    const sp = new URLSearchParams(window.location.search);
    const rk = sp.get('roomKey') || sp.get('room');
    if (rk && (loc.pathname === '/' || loc.pathname === '')) {
      nav(`/room/${encodeURIComponent(rk)}`, { replace: true });
    }
  }, [loc.pathname, nav]);
  if (loc.pathname === '/' || loc.pathname === '') return <Home />;
  if (loc.pathname.startsWith('/room')) return <Room />;
  return <div className="p-6">Not Found</div>;
}

export type AppProps = {
  serverOrigin?: string;
  defaultRoomKey?: string;
  mediaBase?: string;
};

export default function App(props: AppProps) {
  const [apiDown, setApiDown] = useState(false);
  const [checking, setChecking] = useState(true);

  // Merge props with defaults
  const config: WatchpartyConfig = {
    serverOrigin: props.serverOrigin || DEFAULT_CONFIG.serverOrigin,
    defaultRoomKey: props.defaultRoomKey || DEFAULT_CONFIG.defaultRoomKey,
    mediaBase: props.mediaBase || DEFAULT_CONFIG.mediaBase,
  };

  // Check API health on mount
  useEffect(() => {
    async function checkHealth() {
      const origin = config.serverOrigin.replace(/\/$/, '');
      const url = origin + '/healthz';
      try {
        const res = await fetch(url, { 
          cache: 'no-store',
          signal: AbortSignal.timeout(5000) // 5s timeout
        });
        if (res.ok) {
          setApiDown(false);
        } else {
          setApiDown(true);
        }
      } catch (e) {
        setApiDown(true);
      } finally {
        setChecking(false);
      }
    }
    checkHealth();
  }, [config.serverOrigin]);

  // Defer sprite preloading until after page load & idle to avoid early aborted requests noise.
  useEffect(() => {
    function preloadAll() {
      for (const c of CHARACTER_ORDER) {
        const img = new Image();
        img.decoding = 'async';
        img.loading = 'eager';
        img.src = spriteUrlFor(c);
      }
    }
    function schedule() {
      if ('requestIdleCallback' in window) {
        (window as any).requestIdleCallback(preloadAll, { timeout: 2000 });
      } else {
        setTimeout(preloadAll, 600);
      }
    }
    if (document.readyState === 'complete') schedule(); else window.addEventListener('load', schedule, { once: true });
  }, []);

  // Show loading state briefly while checking
  if (checking) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black">
        <div className="text-slate-400">Checking server status...</div>
      </div>
    );
  }

  // Show fallback if API is down
  if (apiDown) {
    return <APIDownFallback />;
  }

  return (
    <ConfigContext.Provider value={config}>
      <div className="min-h-screen flex flex-col">
        <header className="p-3 border-b border-slate-700 flex items-center gap-4 text-sm">
          <Link to="/" className="font-semibold">Watchparty</Link>
        </header>
        <main className="flex-1">
          <RouteBody />
          <Outlet />
        </main>
        <Toasts />
      </div>
    </ConfigContext.Provider>
  );
}
