import { isVerbose } from './socket';
import { getRuntimeConfig } from './config';

export interface CatalogDir { name: string; path: string; }
export interface CatalogFile { id: string; title: string; masterUrl: string; poster: string | null; durationMs: number | null; }
export interface CatalogResponse { dirs: CatalogDir[]; files: CatalogFile[]; }

let cache: { ts: number; prefix: string; data: CatalogResponse } | null = null;
const CACHE_MS = 60000;

export async function fetchCatalog(prefix: string = ''): Promise<CatalogResponse> {
  const now = Date.now();
  if (cache && cache.prefix === prefix && (now - cache.ts) < CACHE_MS) {
    return cache.data;
  }
  const config = getRuntimeConfig();
  const origin = config.serverOrigin.replace(/\/$/, '');
  const url = `${origin}/api/catalog${prefix ? `?prefix=${encodeURIComponent(prefix)}` : ''}`;
  const res = await fetch(url, { cache: 'no-store' });
  if (!res.ok) throw new Error(`catalog_http_${res.status}`);
  const data = await res.json();
  if (isVerbose()) console.debug('[wp] catalog_fetch', { prefix, dirs: data.dirs?.length, files: data.files?.length });
  cache = { ts: now, prefix, data };
  return data;
}

export function invalidateCatalog(prefix?: string) {
  if (!cache) return;
  if (!prefix || cache.prefix === prefix) cache = null;
}
