import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { fetchCatalog, invalidateCatalog } from '../lib/catalog';
import { sendLoad, sendHome } from '../lib/controls';
import { isVerbose } from '../lib/socket';
import { probeMediaEndpoints } from '../lib/catalogProbe';

interface MediaDrawerProps { open: boolean; onClose: () => void; }
interface EpisodeNode { type: 'episode'; name: string; mediaId: string; }
interface SeriesNode { type: 'series'; name: string; episodes: EpisodeNode[]; expanded?: boolean; }
interface CategoryNode { type: 'category'; name: string; series: SeriesNode[]; expanded?: boolean; }
type MediaTree = CategoryNode[];

export const MediaDrawer: React.FC<MediaDrawerProps> = ({ open, onClose }) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [tree, setTree] = useState<MediaTree>([]);
  const [filter, setFilter] = useState('');

  const buildTree = useCallback(async () => {
    setLoading(true); setError(null);
    try {
      const categories: { key: string; label: string }[] = [
        { key: 'anime', label: 'Anime' },
        { key: 'movie', label: 'Movie' },
        { key: 'tv', label: 'TV' }
      ];
      const root = 'output/fmp4';
      const result: MediaTree = [];
      for (const cat of categories) {
        const catPrefix = `${root}/${cat.key}`;
        let catCatalog; try { catCatalog = await fetchCatalog(catPrefix); } catch { continue; }
        const seriesNodes: SeriesNode[] = [];
        for (const d of catCatalog.dirs) {
          let seriesCatalog; try { seriesCatalog = await fetchCatalog(d.path); } catch { continue; }
          const episodes: EpisodeNode[] = seriesCatalog.files.map(f => ({ type: 'episode', name: f.title, mediaId: f.id }));
          if (episodes.length) seriesNodes.push({ type: 'series', name: d.name, episodes, expanded: false });
        }
        if (seriesNodes.length) result.push({ type: 'category', name: cat.label, series: seriesNodes, expanded: true });
      }
      setTree(result);
      if (isVerbose()) probeMediaEndpoints();
    } catch (e:any) {
      setError(e.message || 'catalog_error');
    } finally { setLoading(false); }
  }, []);

  useEffect(() => { if (open) buildTree(); }, [open, buildTree]);

  function handleEpisodeSelect(mediaId: string) {
    sendLoad(mediaId);
    if (isVerbose()) console.debug('[wp] control_load', mediaId);
    onClose();
  }
  function handleHome() {
    sendHome();
    if (isVerbose()) console.debug('[wp] control_home');
    onClose();
  }
  function toggleSeries(ci: number, si: number) {
    setTree(t => t.map((c,i)=> i!==ci? c : ({ ...c, series: c.series.map((s,j)=> j!==si? s : ({ ...s, expanded: !s.expanded })) })));
  }
  function toggleCategory(ci: number) {
    setTree(t => t.map((c,i)=> i!==ci? c : ({ ...c, expanded: !c.expanded })));
  }
  function refreshAll() {
    invalidateCatalog('');
    buildTree();
  }

  const filteredTree = useMemo(() => {
    if (!filter.trim()) return tree;
    const q = filter.toLowerCase();
    return tree.map(cat => {
      const seriesFiltered = cat.series.map(s => {
        const eps = s.episodes.filter(e => e.name.toLowerCase().includes(q));
        if (eps.length) return { ...s, episodes: eps, expanded: true } as SeriesNode;
        if (s.name.toLowerCase().includes(q)) return { ...s, expanded: true } as SeriesNode;
        return null;
      }).filter(Boolean) as SeriesNode[];
      if (seriesFiltered.length) return { ...cat, series: seriesFiltered, expanded: true } as CategoryNode;
      if (cat.name.toLowerCase().includes(q)) return { ...cat, expanded: true } as CategoryNode;
      return null;
    }).filter(Boolean) as MediaTree;
  }, [tree, filter]);

  if (!open) return null;

  return (
    <div className="fixed inset-0 bg-black/40 flex justify-end z-40">
      <div className="w-[440px] h-full bg-gray-950 border-l border-gray-800 flex flex-col">
        <div className="p-3 border-b border-gray-800 flex items-center gap-2 text-sm">
          <button onClick={onClose} className="px-2 py-1 bg-gray-800 hover:bg-gray-700 rounded">Close</button>
          <button onClick={handleHome} className="px-2 py-1 bg-indigo-600 hover:bg-indigo-500 rounded">Home</button>
          <button onClick={refreshAll} className="px-2 py-1 bg-gray-800 hover:bg-gray-700 rounded">Reload</button>
          <input value={filter} onChange={e=>setFilter(e.target.value)} placeholder="search" className="ml-auto bg-gray-900 border border-gray-700 focus:border-gray-500 outline-none px-2 py-1 rounded text-xs" />
        </div>
        {loading && <div className="px-4 py-2 text-xs text-gray-500">Loading catalog</div>}
        {error && <div className="px-4 py-2 text-xs text-red-400">{error}</div>}
        <div className="flex-1 overflow-auto text-sm py-2">
          {filteredTree.map((cat, ci) => (
            <div key={cat.name} className="px-3 pb-3">
              <div className="flex items-center gap-2 cursor-pointer select-none group" onClick={()=>toggleCategory(ci)}>
                <span className="text-xs uppercase tracking-wider text-gray-500 group-hover:text-gray-300">{cat.name}</span>
                <span className="text-[10px] opacity-50">{cat.expanded? '':'+'}</span>
              </div>
              {cat.expanded && (
                <div className="mt-2 space-y-2">
                  {cat.series.map((s, si) => (
                    <div key={s.name}>
                      <div className="flex items-center gap-2 cursor-pointer text-gray-300 hover:text-white" onClick={()=>toggleSeries(ci, si)}>
                        <span className="text-xs font-medium">{s.name}</span>
                        <span className="text-[10px] opacity-40">{s.expanded? '':'+'}</span>
                      </div>
                      {s.expanded && (
                        <div className="mt-1 ml-3 border-l border-gray-800 pl-3 space-y-1">
                          {s.episodes.map(ep => (
                            <button key={ep.mediaId} onClick={()=>handleEpisodeSelect(ep.mediaId)} className="w-full text-left px-2 py-1 rounded hover:bg-gray-900 flex items-center gap-2 group">
                              <span className="text-[11px] text-gray-400 group-hover:text-gray-200 truncate">{ep.name}</span>
                              <span className="ml-auto text-[10px] text-indigo-400 opacity-0 group-hover:opacity-100 transition-opacity">Load</span>
                            </button>
                          ))}
                          {s.episodes.length === 0 && <div className="text-[11px] text-gray-600 italic">(no episodes)</div>}
                        </div>
                      )}
                    </div>
                  ))}
                  {cat.series.length === 0 && <div className="text-[11px] text-gray-600 italic">(empty)</div>}
                </div>
              )}
            </div>
          ))}
          {!loading && !filteredTree.length && <div className="px-4 py-6 text-center text-xs text-gray-600">No matches</div>}
        </div>
        <div className="p-2 text-[10px] text-gray-600 border-t border-gray-900 flex justify-between">
          <span>Simple Catalog</span>
          {isVerbose() && <span className="opacity-70">verbose</span>}
        </div>
      </div>
    </div>
  );
};
