import React, { useEffect, useState } from 'react';
import { useAppStore } from '../lib/store';
import PixelAvatar from './PixelAvatar';
import { sendIdentityUpdate } from '../lib/socket';
import { CHARACTER_ORDER, spriteUrlFor } from '../lib/characters';

interface Props { open: boolean; onClose: () => void; onJoin: () => void; }

export default function CharacterDrawer({ open, onClose }: Props) {
  const { name, sprite, setIdentity } = useAppStore();
  const [localName, setLocalName] = useState(name);
  const [localSprite, setLocalSprite] = useState<string | null>(sprite);
  const [mounted, setMounted] = useState(open);
  useEffect(() => {
    if (open) {
      setMounted(true);
    } else {
      const t = setTimeout(() => setMounted(false), 320); // allow transition
      return () => clearTimeout(t);
    }
  }, [open]);

  const updateIdentity = () => {
    const finalName = (localName || '').trim().slice(0,32) || 'Guest';
    setIdentity(finalName, localSprite);
    sendIdentityUpdate(finalName, localSprite || undefined);
    onClose();
  };

  if (!mounted) return null;

  return (
    <div aria-hidden={!open} className={`fixed inset-y-0 right-0 w-80 bg-slate-900/95 backdrop-blur border-l border-slate-700 transition-transform duration-300 z-30 flex flex-col ${open ? 'translate-x-0' : 'translate-x-full'} ${open ? '' : 'pointer-events-none'}`}>
      <div className="p-4 flex items-center justify-between border-b border-slate-700">
        <h2 className="font-semibold text-sm">Identity</h2>
  <button type="button" onClick={onClose} role="button" className="text-xs opacity-70 hover:opacity-100 select-none">Close</button>
      </div>
      <div className="p-4 space-y-4 overflow-y-auto text-sm">
        <div>
          <label className="block text-xs uppercase tracking-wide mb-1 opacity-70">Display Name</label>
          <input value={localName} onChange={e=>setLocalName(e.target.value)} className="w-full bg-slate-800 border border-slate-600 rounded px-2 py-1 text-sm" placeholder="Your name" />
        </div>
        <div>
          <label className="block text-xs uppercase tracking-wide mb-1 opacity-70">Character</label>
          <div className="grid grid-cols-4 gap-3">
            {CHARACTER_ORDER.map(c => (
              <button key={c} onClick={()=>{ setLocalSprite(c); if (!localName.trim() || CHARACTER_ORDER.includes(localName as any) || localName === 'Guest') setLocalName(c); }} className={`relative group rounded border ${localSprite===c ? 'border-emerald-400' : 'border-slate-600 hover:border-slate-400'} p-1 flex flex-col items-center gap-1`}> 
                <img src={spriteUrlFor(c)} alt={c} className="w-12 h-12 object-cover rounded" />
                <span className="text-[10px] opacity-70 group-hover:opacity-100 capitalize">{c}</span>
              </button>
            ))}
          </div>
        </div>
        <div className="pt-2 flex gap-2 items-center">
          <button onClick={updateIdentity} className="bg-emerald-500 hover:bg-emerald-400 text-black font-semibold text-sm px-3 py-1 rounded">Update</button>
        </div>
      </div>
    </div>
  );
}
