import React, { useEffect, useMemo, useRef, useState } from 'react';
import StarfieldCanvas from '../components/StarfieldCanvas';
import Roster from '../components/Roster';
import MediaPlayer from '../components/MediaPlayer';
import { MediaDrawer } from '../components/MediaDrawer';
import { useAppStore } from '../lib/store';
import CharacterDrawer from '../components/CharacterDrawer';
import { connectAndJoin } from '../lib/socket';
import { useParams } from 'react-router-dom';
import { useConfig } from '../lib/config';

function resolveMedia(mediaId: string | null, mediaBase: string) {
  if (!mediaId) return null;
  // mediaId is a relative path under MEDIA_ROOT returned by server (catalog id)
  const root = `${mediaBase.replace(/\/$/, '')}/${mediaId}`.replace(/\\/g,'/');
  return { mediaUrl: `${root}/output_frag.mp4`, indexUrl: `${root}/index.json` };
}

export default function Room() {
  const config = useConfig();
  const snapshot = useAppStore(s => s.snapshot);
  const { ensureAutoIdentity, name, sprite } = useAppStore();
  const params = useParams();
  const joinedRef = useRef(false);
  const mediaInfo = useMemo(() => resolveMedia(snapshot.mediaId, config.mediaBase), [snapshot.mediaId, config.mediaBase]);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [identityOpen, setIdentityOpen] = useState(false);
  useEffect(() => {
    if (joinedRef.current) return;
    const sp = new URLSearchParams(window.location.search);
    const roomKey = params.roomKey || sp.get('room') || config.defaultRoomKey;
    ensureAutoIdentity();
    // Accept multiple casings for leader key param
    const leaderKeyParam = sp.get('leaderKey') || sp.get('leaderkey') || sp.get('leader_key') || undefined;
    // Only send leader_key if explicitly supplied (no implicit dev fallback granting everyone leader rights)
    connectAndJoin({ room_key: roomKey, leader_key: leaderKeyParam || null, name: name || undefined, sprite: sprite || undefined });
    joinedRef.current = true;
  }, [params.roomKey, ensureAutoIdentity, name, sprite, config.defaultRoomKey]);
  const isLeader = useAppStore(s => !!s.snapshot.leaderId && s.snapshot.leaderId === s.me.clientId);

  function openMedia() {
    setIdentityOpen(false);
    setDrawerOpen(true);
  }
  function openIdentity() {
    setDrawerOpen(false);
    setIdentityOpen(true);
  }
  function closeMedia() { setDrawerOpen(false); }
  function closeIdentity() { setIdentityOpen(false); }
  return (
    <div className="relative min-h-[calc(100vh-120px)] flex">
      <div className="flex-1 relative bg-black">
        {mediaInfo ? (
          <MediaPlayer mediaUrl={mediaInfo.mediaUrl} indexUrl={mediaInfo.indexUrl} mediaId={snapshot.mediaId!} />
        ) : (
          <>
            <StarfieldCanvas />
            <div className="absolute inset-0 flex flex-col items-center justify-center gap-4 p-6">
              {/* Removed placeholder instructional texts per spec */}
              <div className="mt-6 p-3 rounded bg-slate-900/70 border border-slate-700/40 backdrop-blur-sm">
                <Roster />
              </div>
            </div>
          </>
        )}
        {isLeader && !drawerOpen && (
          <button onClick={openMedia} className="fixed bottom-4 left-1/2 -translate-x-1/2 z-40 bg-indigo-600 hover:bg-indigo-500 text-xs px-4 py-2 rounded shadow">
            Media
          </button>
        )}
      </div>
      <aside className="w-80 border-l border-slate-800 p-4 hidden md:block text-xs opacity-70">
        Chat panel placeholder (Phase 5)
      </aside>
      <MediaDrawer open={drawerOpen} onClose={closeMedia} />
      <CharacterDrawer open={identityOpen} onClose={closeIdentity} onJoin={()=>{}} />
      {!identityOpen && (
        <button onClick={openIdentity} className="fixed bottom-4 right-4 bg-slate-800/80 hover:bg-slate-700 border border-slate-600 text-xs px-3 py-1 rounded shadow backdrop-blur z-40">Identity</button>
      )}
    </div>
  );
}
