import { createContext, useContext } from 'react';

export interface WatchpartyConfig {
  serverOrigin: string;
  defaultRoomKey: string;
  mediaBase: string;
}

export const DEFAULT_CONFIG: WatchpartyConfig = {
  serverOrigin: 'http://localhost:8080',
  defaultRoomKey: 'dev-room-1000',
  mediaBase: '/media',
};

// Runtime config used by non-component code (socket, catalog, etc.)
let runtimeConfig: WatchpartyConfig = { ...DEFAULT_CONFIG };

export function setRuntimeConfig(config: Partial<WatchpartyConfig>) {
  runtimeConfig = { ...runtimeConfig, ...config };
}

export function getRuntimeConfig(): WatchpartyConfig {
  return runtimeConfig;
}

// React Context for component tree
export const ConfigContext = createContext<WatchpartyConfig>(DEFAULT_CONFIG);

export function useConfig(): WatchpartyConfig {
  return useContext(ConfigContext);
}
