import React from 'react';
import { useAppStore } from '../lib/store';
import { CHARACTER_COLORS, spriteUrlFor } from '../lib/characters';

export default function Roster() {
  const roster = useAppStore(s => s.roster);
  const users = Object.values(roster);
  if (!users.length) return <div className="text-xs opacity-50">No users</div>;
  return (
    <ul className="text-xs space-y-1">
      {users.map(u => {
        const color = (u.name && (CHARACTER_COLORS as any)[u.name]) || '#cbd5e1';
        return (
          <li key={u.clientId} className="flex items-center gap-2">
            {u.sprite ? (
              <img src={spriteUrlFor(u.sprite as any)} alt={u.sprite} className="w-7 h-7 rounded object-cover" />
            ) : (
              <span className="inline-block w-7 h-7 bg-slate-700/50 rounded" />
            )}
            <span className="truncate max-w-[120px] font-medium" style={{ color }}>{u.name || 'Anon'}</span>
            {u.isLeader && <span className="px-1 rounded bg-emerald-600 text-[9px]">L</span>}
          </li>
        );
      })}
    </ul>
  );
}
