import React, { useEffect } from 'react';
import { useAppStore } from '../lib/store';

export default function Toasts() {
  const toasts = useAppStore(s => s.toasts);
  const pop = useAppStore(s => s.popToast);
  useEffect(() => {
    const timers = toasts.map(t => setTimeout(() => pop(t.id), 4000));
    return () => { timers.forEach(clearTimeout); };
  }, [toasts, pop]);
  return (
    <div className="fixed bottom-4 left-1/2 -translate-x-1/2 flex flex-col gap-2 z-50">
      {toasts.map(t => (
        <div key={t.id} className={`px-3 py-2 rounded text-sm shadow font-medium bg-slate-800 border ${t.kind==='error' ? 'border-red-500 text-red-300' : 'border-slate-600 text-slate-200'}`}>{t.msg}</div>
      ))}
    </div>
  );
}
