import { io, Socket } from 'socket.io-client';
import { useAppStore, RosterUser, ChatMessage } from './store';
import { getRuntimeConfig } from './config';

// Verbose flag detection (URL ?verbose or ?v=1, or localStorage wp_verbose=1)
const VERBOSE = (() => {
  try {
    const sp = new URLSearchParams(window.location.search);
    if (sp.has('verbose') || sp.get('v') === '1') return true;
    if (localStorage.getItem('wp_verbose') === '1') return true;
  } catch (_) { /* noop */ }
  return false;
})();

function vLog(label: string, payload?: any) {
  if (!VERBOSE) return;
  if (payload !== undefined) console.debug('[wp]', label, payload); else console.debug('[wp]', label);
}

export function isVerbose() { return VERBOSE; }

export async function probeServerHealth() {
  const config = getRuntimeConfig();
  const origin = config.serverOrigin.replace(/\/$/, '');
  const url = origin + '/healthz';
  try {
    const t0 = performance.now();
    const res = await fetch(url, { cache: 'no-store' });
    const text = await res.text().catch(()=>'<no-body>');
    vLog('http_probe', { url, status: res.status, ok: res.ok, ms: Math.round(performance.now()-t0), body: text.slice(0,60) });
  } catch (e:any) {
    vLog('http_probe_fail', { url, error: e?.message || String(e) });
  }
}

let socket: Socket | null = null;
let timePingInflight: { ts: number } | null = null;
let timePingInterval: any = null;

interface JoinOpts {
  room_key: string;
  leader_key?: string | null;
  override?: boolean;
  name?: string;
  sprite?: string | null;
}

export function connectAndJoin(opts: JoinOpts) {
  const s = getSocket();
  const state = useAppStore.getState();
  // If we already have a clientId and roster contains it, avoid duplicate join.
  if (state.me.clientId && state.roster[state.me.clientId]) {
    if (opts.name || opts.sprite) {
      vLog('identity_update_emit', { name: opts.name, sprite: opts.sprite });
      s.emit('identity_update', { name: opts.name, sprite: opts.sprite });
    }
    return s;
  }
  const payload = sanitizeJoin(opts);
  const postJoinIdentity = () => {
    setTimeout(() => {
      const st = useAppStore.getState();
      if (st.name) {
        vLog('identity_update_emit_post_join', { name: st.name, sprite: st.sprite });
        s.emit('identity_update', { name: st.name, sprite: st.sprite || undefined });
      }
    }, 40); // small delay to ensure server processed join
  };
  if (s.connected) {
    vLog('join_emit', payload);
    s.emit('join', payload);
    postJoinIdentity();
  } else {
    s.once('connect', () => { vLog('join_emit', payload); s.emit('join', payload); postJoinIdentity(); });
  }
  return s;
}

function sanitizeJoin(o: JoinOpts) {
  return {
    room_key: o.room_key,
    leader_key: o.leader_key || undefined,
    override: o.override || undefined,
    name: o.name,
    sprite: o.sprite
  };
}

let RESOLVED_ORIGIN: string | null = null;
export function getResolvedOrigin() { return RESOLVED_ORIGIN; }

export function getSocket(): Socket {
  if (!socket) {
    const config = getRuntimeConfig();
    let origin = config.serverOrigin || '/';
    if (origin === '/' && typeof window !== 'undefined') {
      const loc = window.location;
      if ((loc.hostname === 'localhost' || loc.hostname === '127.0.0.1') && loc.port === '5173') {
        origin = `${loc.protocol}//${loc.hostname}:8080`;
        vLog('socket_origin_fallback', { origin });
      }
    }
    RESOLVED_ORIGIN = origin;
    socket = io(origin, { path: '/watchparty/ws', transports: ['websocket'] });
    vLog('socket_init', { origin });
    socket.on('connect', () => {
      const sid = socket?.id;
      vLog('socket_connect', { id: sid });
      if (sid) {
        try { useAppStore.getState().ensureMe(sid); } catch (_) {}
      }
    });
    socket.on('disconnect', (reason) => vLog('socket_disconnect', { reason }));
  socket.on('connect_error', (err) => vLog('socket_connect_error', { message: err?.message }));
  socket.on('reconnect_attempt', (n) => vLog('socket_reconnect_attempt', { attempt: n }));
  socket.on('reconnect_failed', () => vLog('socket_reconnect_failed'));
    wireCoreHandlers(socket);
  }
  return socket;
}

function wireCoreHandlers(s: Socket) {
  const store = useAppStore.getState();
  // snapshot
  s.on('snapshot', (payload: any) => {
    if (payload?.client_id) useAppStore.getState().ensureMe(payload.client_id);
    const users: RosterUser[] = (payload?.users || []).map((u: any) => ({
      clientId: u.client_id,
      name: u.name || 'Anon',
      sprite: u.sprite_id ?? u.sprite ?? null,
      isLeader: u.client_id === payload.leader_id
    }));
    useAppStore.getState().applySnapshot({
      mediaId: payload.media_id ?? null,
      playheadMs: payload.playhead_ms || 0,
      playing: !!payload.playing,
      serverSeq: payload.server_seq || 0,
      leaderId: payload.leader_id || null,
      users
    });
    // Auto identity assignment if we still have none
    const st = useAppStore.getState();
    if (!st.name) {
      st.ensureAutoIdentity?.();
      const st2 = useAppStore.getState();
      if (st2.name) {
        vLog('identity_auto_assign', { name: st2.name, sprite: st2.sprite });
        s.emit('identity_update', { name: st2.name, sprite: st2.sprite || undefined });
      }
    }
    vLog('snapshot', payload);
  });

  // start time ping loop (client-initiated) every 5s if not already
  if (!timePingInterval) {
    timePingInterval = setInterval(() => {
      initiateTimePing();
    }, 5000);
  }

  // presence events
  s.on('presence', (ev: any) => {
    if (!ev) return;
    // Server currently emits { users: [...] }
    if (Array.isArray(ev.users)) {
      const roster: Record<string, RosterUser> = {};
      for (const u of ev.users) {
        roster[u.client_id] = {
          clientId: u.client_id,
          name: u.name || 'Anon',
          sprite: u.sprite_id ?? u.sprite ?? null,
          isLeader: u.is_leader
        };
      }
      // If our local identity is known but server omitted it (race), patch it in
      const st = useAppStore.getState();
      const myId = socket?.id;
      if (myId && roster[myId]) {
        if (st.name) roster[myId].name = st.name; // authoritative locally chosen name
        if (st.sprite) roster[myId].sprite = st.sprite;
      }
      // Replace roster wholesale for simplicity
      (st as any).roster = roster; // direct mutate not ideal; use set
  useAppStore.setState({ roster });
    } else if (ev.kind && ev.user) { // fallback legacy granular events
      if (ev.kind === 'leave') {
        useAppStore.getState().removeUser(ev.user.client_id);
      } else { // join/update
        useAppStore.getState().upsertUser({
          clientId: ev.user.client_id,
          name: ev.user.name || '',
          sprite: ev.user.sprite_id ?? ev.user.sprite ?? null,
          isLeader: ev.user.is_leader
        });
        // Patch local identity if ours
        const st2 = useAppStore.getState();
        const myId2 = socket?.id;
        if (myId2 && myId2 === ev.user.client_id && st2.name) {
          useAppStore.getState().upsertUser({
            clientId: myId2,
            name: st2.name,
            sprite: st2.sprite,
            isLeader: ev.user.is_leader
          });
        }
      }
    }
    vLog('presence', ev);
    // If we still have no identity after presence, attempt assignment
    const stFinal = useAppStore.getState();
    if (!stFinal.name) {
      stFinal.ensureAutoIdentity?.();
      const st3 = useAppStore.getState();
      if (st3.name) {
        vLog('identity_auto_assign_presence', { name: st3.name, sprite: st3.sprite });
        s.emit('identity_update', { name: st3.name, sprite: st3.sprite || undefined });
      }
    }
  });

  // control broadcasts (simplified before MSE): { server_seq, playhead_ms, playing, leader_id? }
  s.on('control_broadcast', (msg: any) => {
    const state = useAppStore.getState();
    // If media_id present and changed, promote it into snapshot + reset readiness gate
    if (Object.prototype.hasOwnProperty.call(msg, 'media_id')) {
      const newMediaId = msg.media_id ?? null;
      if (state.snapshot.mediaId !== newMediaId) {
        state.applySnapshot({ mediaId: newMediaId });
        // Reset readiness (server will emit fresh ready_state after clients append)
        if (newMediaId) state.setReadiness({ mediaId: newMediaId, ready: false, readyCount: 0, total: 0 });
      }
    }
    state.updateControlState({
      playheadMs: msg.playhead_ms,
      playing: msg.playing,
      serverSeq: msg.server_seq,
      leaderId: msg.leader_id ?? state.snapshot.leaderId
    });
    vLog('control_broadcast', msg);
  });

  // readiness gate updates
  s.on('ready_state', (rs: any) => {
    if (!rs) return;
    useAppStore.getState().setReadiness({
      mediaId: rs.media_id,
      ready: !!rs.ready,
      readyCount: rs.ready_count ?? rs.readyCount ?? 0,
      total: rs.total ?? 0
    });
    vLog('ready_state', rs);
  });

  // control ack (map client_seq -> server_seq)
  s.on('control_ack', (ack: any) => {
    if (typeof ack?.client_seq === 'number' && typeof ack?.server_seq === 'number') {
      useAppStore.getState().recordAck(ack.client_seq, ack.server_seq);
      vLog('control_ack', ack);
    }
  });

  // time ping from server: { id, server_time_ms }
  s.on('time_ping', (p: any) => {
    // respond immediately; include original id if present
    s.emit('time_pong', { id: p?.id });
    if (timePingInflight && timePingInflight.ts) {
      const rtt = Date.now() - timePingInflight.ts;
      useAppStore.getState().addRttSample(rtt, p?.server_time_ms, timePingInflight.ts);
      timePingInflight = null;
      vLog('time_rtt', { rtt, offset: useAppStore.getState().drift.offsetMs });
    }
  });

  // chat_broadcast: { id, client_id, text, name, sprite, ts }
  s.on('chat_broadcast', (m: any) => {
    const chatMsg: ChatMessage = {
      id: String(m.id ?? m.server_seq ?? Date.now()),
      clientId: m.client_id,
      name: m.name || 'Anon',
      sprite: m.sprite ?? null,
      text: m.text || '',
      ts: m.ts || Date.now()
    };
    useAppStore.getState().appendChat(chatMsg);
    vLog('chat', chatMsg);
  });

  // error: { code, message? }
  s.on('error', (e: any) => {
    console.warn('[error]', e);
    const code = e?.code || 'error/unknown';
    const map: Record<string, string> = {
      'control/forbidden': 'You are not the leader.',
      'control/stale_seq': 'Stale control sequence.',
      'control/seq_gap': 'Control sequence gap detected.',
      'rate/limited': 'Rate limited. Slow down.',
      'payload/too_large': 'Payload too large.',
      'room/full': 'Room is full.',
      'auth/invalid_key': 'Invalid leader key.',
      'origin_block': 'Origin not allowed.'
    };
    useAppStore.getState().pushToast('error', map[code] || code);
  });
}

// outbound helpers
export function sendControl(kind: 'play' | 'pause' | 'seek', extra?: any) {
  const state = useAppStore.getState();
  const client_seq = state.allocateClientSeq();
  const payload = { kind, client_seq, ...extra };
  getSocket().emit('control', payload);
  return client_seq;
}

export function sendChat(text: string) {
  getSocket().emit('chat_send', { text });
}

export function initiateTimePing() {
  if (timePingInflight) return; // wait for prior
  timePingInflight = { ts: Date.now() };
  getSocket().emit('time_ping', {});
}

export function sendIdentityUpdate(name?: string, sprite?: string | null) {
  const payload: any = {};
  if (name) payload.name = name;
  if (sprite) payload.sprite = sprite;
  if (!payload.name && !payload.sprite) return;
  vLog('identity_update_emit', payload);
  getSocket().emit('identity_update', payload);
}
