import React, { useEffect, useRef } from 'react';

interface Star {
  x: number; y: number; z: number; speed: number; size: number; alpha: number;
  // Color twinkle / rare hue cycling
  colorRare: boolean; colorPhase: number; colorSpeed: number; colorMode: number;
  // Brightness twinkle (new model)
  twinkleAmount: number; // amplitude factor (0..~0.5 used to derive dim floor)
  twinkleEnabled: boolean; // whether this star participates in brightness interpolation
  dimFloor: number; // fraction of alpha at darkest (0..1)
  twinkleStart: number; // starting alpha value for current segment
  twinkleTarget: number; // target alpha value for current segment
  twinkleT: number; // progress time (frame units) in current segment
  twinkleDuration: number; // total duration (frame units) of current segment
  microPhase: number; // small fast phase for subtle sparkle (ultra mostly)
  microSpeed: number;
  ultraRare: boolean; trail?: { x: number; y: number }[]; maxTrail?: number; currentAlpha?: number;
  lifeMsRemaining: number; // remaining life in milliseconds
  lifeMsTotal: number; // total assigned life
}

const STAR_COUNT = 400; 

export default function StarfieldCanvas() {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const starsRef = useRef<Star[]>([]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    const resize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };
    resize();
    window.addEventListener('resize', resize);

  let creationCounter = 0; // tracks total stars ever created (including replacements) if we decide to extend taper later

  function biasForIndex(i: number) {
    if (i < 300) return { scale: 0.5, demote: 0.5 };      // first 300 -> 50%
    if (i < 400) return { scale: 0.6, demote: 0.4 };      // next 100 -> 40%
    if (i < 500) return { scale: 0.7, demote: 0.3 };      // next 100 -> 30%
    if (i < 600) return { scale: 0.8, demote: 0.2 };      // next 100 -> 20%
    if (i < 700) return { scale: 0.9, demote: 0.1 };      // next 100 -> 10%
    return { scale: 1.0, demote: 0 };                     // afterward -> no reduction
  }

  function makeStar(x?: number, y?: number, initial=false, index?: number): Star {
      // Bias toward smaller z (slower/smaller) making large/bright rare
      let r = Math.random();
      // Determine bias bucket
      const creationIndex = index ?? creationCounter;
      if (index === undefined) creationCounter++;
      const bias = initial ? biasForIndex(creationIndex) : { scale: 1.0, demote: 0 };
  let ultraRare = r > 0.998; // base ultra threshold (~0.2%)
  let rare = !ultraRare && r > 0.98; // base rare threshold (~2%)
      if (bias.demote > 0) {
        if (ultraRare && Math.random() < bias.demote) ultraRare = false;
        if (rare && Math.random() < bias.demote) rare = false;
      }
      // If this is part of the initial population, suppress rare / ultra entirely per request
      if (initial) {
        ultraRare = false;
        rare = false;
      }
  // Color-twinkle rare: pick from remaining base population (~1% of non-rare/non-ultra)
  const colorRare = rare; // only rare stars twinkle (whiteblue or whitered)
      let z: number;
      if (ultraRare) {
        // Force ultra-rare into highest depth band so they are always fast/bright
        z = 0.92 + Math.random() * 0.08; // 0.92 - 1.0
      } else if (rare) {
        z = 0.85 + Math.random() * 0.15;
      } else {
        z = 0.05 + (Math.random() ** 2.2) * 0.75; // mostly 0.05-0.6 range
      }
        // Utility triangular distribution (center bias => extremes rarer)
        const tri = (min: number, max: number) => {
          const u1 = Math.random();
          const u2 = Math.random();
          return min + ((u1 + u2) / 2) * (max - min);
        };
        // Broaden size range with center bias; ultra can get larger; allow smaller floor
  let sizeBase = tri(0.9, 1.4) + (z ** 1.2) * (ultraRare ? 5.5 : rare ? 4.0 : 3.2);
        const sizeVariance = tri(0.85, 1.25); // multiplicative tweak (extremes rarer)
  let size = sizeBase * sizeVariance;
  if (initial && bias.scale < 1) size *= bias.scale; // tapered size reduction
        // Broaden alpha range; allow overbright (>1) which will be rendered with an additive pass
        let alpha: number;
        if (ultraRare) {
          // Ultra: high baseline with large possible overbright tail (1.05 - 1.55)
            alpha = 1.05 + Math.random() * 0.50; 
        } else if (rare) {
          // Rare: mid-high brightness (approx 0.65 - 1.25)
          alpha = 0.55 + (z ** 1.05) * 0.70 + Math.random() * 0.25; // typical ~0.8-1.1
          if (alpha > 1.25) alpha = 1.25;
        } else {
          // Common: ~0.15 - 1.05 (most <0.9)
          alpha = 0.15 + (z ** 1.05) * 0.60 + Math.random() * 0.15;
          if (alpha > 1.05) alpha = 1.05;
        }
  if (!ultraRare && alpha < 0.15) alpha = 0.15; // raise minimal base brightness to 15%
        // Speed now broader with variance factor; maintain z influence
        const speedBase = 0.035 + tri(0, 0.08); // lower floor & occasional higher base
        const speedScale = (z ** 1.45) * (ultraRare ? 4.9 : rare ? 1.55 : 1.25);
  let speed = speedBase + speedScale * tri(0.85, 1.3);
  if (initial && bias.scale < 1) speed *= bias.scale; // tapered speed reduction
        // Additional minor coupling to size/alpha (extremes give mild boost)
        const normAlpha = Math.min(1, alpha);
        const normSize = Math.min(1, (size - 1) / 4.5);
        speed *= 1 + 0.18 * Math.max(normAlpha, normSize);
        if (ultraRare && speed < 1.25) speed = 1.25 + Math.random() * 0.4;
  const cw = canvas!.width;
  const ch = canvas!.height;
      const twinkleAmount = 0.18 + Math.random() * 0.28; // base amplitude for brightness variance
  // Enable twinkle for ~18% of non-ultra stars; ultra always twinkle for distinctiveness
  const twinkleEnabled = ultraRare || Math.random() < 0.18;
      // Deeper dim for common stars, shallower for rare/ultra
      const dimFloor = ultraRare ? 0.55 : (colorRare ? 0.38 : Math.max(0.10, 0.28 - twinkleAmount * 0.25));
  // Immediate visibility: choose a starting brightness within allowed range
  const startVal = alpha * (dimFloor + Math.random() * (1 - dimFloor));
  const targetVal = alpha * (dimFloor + Math.random() * (1 - dimFloor));
      const duration = (ultraRare ? 130 : colorRare ? 160 : 120) + Math.random() * (ultraRare ? 140 : 150); // frame units (~2-5s)
  // Lifetime 10s..120s weighted by speed.
  // All stars can occupy full range; slow stars biased longer (typically >=40s) but still small chance below 40.
  // Fast stars biased shorter. Achieved via exponent shaping + floor lift proportional to (1 - speedNorm).
  const EXPECTED_MAX_SPEED = 2.5; // heuristic ceiling for normalization
  const speedNorm = Math.min(1, speed / EXPECTED_MAX_SPEED);
  const MIN_SEC = 10;
  const MAX_SEC = 120;
  const spanSec = MAX_SEC - MIN_SEC; // 110
  let u = Math.random();
  const exp = 0.8 + speedNorm * 1.4; // slow ~0.8 (bias high), fast ~2.2 (bias low)
  u = Math.pow(u, exp);
  const floorLift = (1 - speedNorm) * ((40 - MIN_SEC) / spanSec); // up to ~0.2727 for slowest so min ~40s typical
  u = u * (1 - floorLift) + floorLift; // raise lower bound proportionally for slow stars
  const lifeMsTotal = (MIN_SEC + u * spanSec) * 1000; // final ms
      return {
  x: x == null ? Math.random() * cw : x,
  y: y != null ? y : (initial ? Math.random() * ch : -Math.random() * ch),
        z,
        speed,
        size,
        alpha,
        colorRare,
        colorPhase: Math.random() * Math.PI * 2,
        colorSpeed: 0.01 + Math.random() * 0.02,
        colorMode: Math.random() < 0.5 ? 0 : 1,
        twinkleAmount,
  twinkleEnabled,
        dimFloor,
        twinkleStart: startVal,
        twinkleTarget: targetVal,
        twinkleT: 0,
        twinkleDuration: duration,
        microPhase: Math.random() * Math.PI * 2,
        microSpeed: (ultraRare ? 0.18 : 0.10) + Math.random() * 0.05,
        ultraRare,
        trail: ultraRare ? [] : undefined,
        currentAlpha: startVal,
        lifeMsRemaining: lifeMsTotal,
        lifeMsTotal
      };
    }

    if (!starsRef.current.length) {
  for (let i = 0; i < STAR_COUNT; i++) starsRef.current.push(makeStar(undefined, undefined, true, i));
    }

    let lastTime = performance.now();
    function drawCurvedBottomRect(x: number, y: number, size: number, speed: number) {
      const maxExpectedSpeed = 2.0;
      const n = Math.min(1, speed / maxExpectedSpeed);
      const bottomR = (0.15 + 0.75 * n) * size; // more curvature with speed
      const w = size;
      const h = size;
      const r = Math.min(bottomR, h * 0.95, w * 0.48);
      const x0 = x;
      const y0 = y;
      const x1 = x + w;
      const y1 = y + h;
      const c = ctx!;
      c.beginPath();
      c.moveTo(x0, y0);
      c.lineTo(x1, y0); // top edge
      c.lineTo(x1, y1 - r);
      c.quadraticCurveTo(x1, y1, x1 - r, y1);
      c.lineTo(x0 + r, y1);
      c.quadraticCurveTo(x0, y1, x0, y1 - r);
      c.lineTo(x0, y0);
      c.closePath();
      c.fill();
    }

    // New brightness model: interpolate between random targets (value noise) to avoid flicker at troughs
    function updateAlpha(s: Star, dt: number) {
      if (dt > 3) dt = 3; // cap runaway dt
      if (!s.twinkleEnabled) { // no brightness interpolation; maintain stable alpha
        s.currentAlpha = s.alpha;
        return;
      }
      s.twinkleT += dt;
      if (s.twinkleT >= s.twinkleDuration) {
        // Start a new segment
        s.twinkleStart = s.twinkleTarget;
        // Pick a new target in [dimFloor*alpha, alpha]
        s.twinkleTarget = s.alpha * (s.dimFloor + Math.random() * (1 - s.dimFloor));
        // Duration with some variability; ultra get longer smoother drifts
        const baseDur = s.ultraRare ? 150 : (s.colorRare ? 140 : 110);
        const jitter = s.ultraRare ? 170 : 140;
        s.twinkleDuration = baseDur + Math.random() * jitter;
        s.twinkleT = 0;
      }
      const p = Math.min(1, s.twinkleT / s.twinkleDuration);
      // Smoothstep easing
      const eased = p * p * (3 - 2 * p);
      let val = s.twinkleStart + (s.twinkleTarget - s.twinkleStart) * eased;
      // Ultra rare micro sparkle (very subtle, high frequency, low amplitude)
      s.microPhase += s.microSpeed * dt;
      if (s.ultraRare) {
        const micro = Math.sin(s.microPhase) * 0.04; // 0.04
        val = Math.min(1, Math.max(s.alpha * s.dimFloor, val * (1 + micro)));
      }
      s.currentAlpha = val;
    }

    // Update only color related state & return fillStyle
    function updateColor(s: Star, dt: number): string {
      if (s.ultraRare) {
        s.colorPhase += (s.colorSpeed * 2.2) * dt; // faster cycle for ultra rare
        const colors = [
          [255, 120, 220], // pink
          [120, 255, 170], // green
          [255, 255, 255]  // white
        ];
        const cycle = s.colorPhase % colors.length;
        const idx = Math.floor(cycle);
        const next = (idx + 1) % colors.length;
        const t = cycle - idx;
        const c0 = colors[idx];
        const c1 = colors[next];
        const rCol = Math.round(c0[0] + (c1[0] - c0[0]) * t);
        const gCol = Math.round(c0[1] + (c1[1] - c0[1]) * t);
        const bCol = Math.round(c0[2] + (c1[2] - c0[2]) * t);
        return `rgb(${rCol},${gCol},${bCol})`;
      }
      if (s.colorRare) {
        s.colorPhase += s.colorSpeed * dt;
        const t = (Math.sin(s.colorPhase) + 1) / 2; // 0..1
        if (s.colorMode === 0) {
          const rCol = Math.round(255 - (255 - 90) * t);
          const gCol = Math.round(255 - (255 - 150) * t);
          const bCol = 255;
          return `rgb(${rCol},${gCol},${bCol})`;
        } else {
          const rCol = 255;
          const gCol = Math.round(255 - (255 - 80) * t);
          const bCol = Math.round(255 - (255 - 80) * t);
          return `rgb(${rCol},${gCol},${bCol})`;
        }
      }
      return '#ffffff';
    }
    const render = () => {
      const now = performance.now();
  const deltaMs = now - lastTime;
  const dt = deltaMs / 16.666; // frame units (~60fps)
      lastTime = now;
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      const w = canvas.width;
      const h = canvas.height;
      const OFFSCREEN_BUFFER = 220; // allow trails to finish below view
      for (let i = 0; i < starsRef.current.length; i++) {
        const s = starsRef.current[i];
        // Maintain/update trail data before drawing
        if (s.ultraRare) {
          if (!s.trail) s.trail = [];
          if (!s.maxTrail) {
            const maxExpectedSpeed = 1.9;
            const base = s.speed * 14;
            const norm = Math.min(1, s.speed / maxExpectedSpeed);
            const scaled = base * (1 + 8 * norm);
            s.maxTrail = Math.min(300, Math.floor(scaled));
          }
          s.trail.push({ x: s.x + s.size / 2, y: s.y + s.size / 2 });
          if (s.trail.length > s.maxTrail) s.trail.splice(0, s.trail.length - s.maxTrail);
        }
        // Lifetime handling
        s.lifeMsRemaining -= deltaMs;
        if (s.lifeMsRemaining <= 0) {
          // Respawn new star from top (non-initial)
          const newStar = makeStar(Math.random() * canvas.width, undefined, false);
          // Position just above view
          newStar.y = -newStar.size - 1;
          starsRef.current[i] = newStar;
          continue;
        }
        s.y += s.speed * dt;
        if (s.y - s.size > canvas.height + OFFSCREEN_BUFFER) {
          starsRef.current[i] = makeStar(Math.random() * canvas.width, -s.size - 1);
          continue;
        }
    updateAlpha(s, dt);
    const baseAlphaRaw = s.currentAlpha ?? s.alpha; // previous baseAlpha replaced by fade logic
    const FADE_MS = 1500; // fade duration window
    const lifeFade = s.lifeMsRemaining < FADE_MS ? Math.max(0, s.lifeMsRemaining / FADE_MS) : 1;
    const baseAlpha = baseAlphaRaw * lifeFade;
    let starFillStyle = updateColor(s, dt);
        if (s.ultraRare) {
          const enhanced = baseAlpha;
          if (s.trail && s.trail.length) {
            const len = s.trail.length;
            for (let ti = 0; ti < len; ti++) {
              const tp = s.trail[len - 1 - ti];
              const linear = (1 - ti / len);
              const fade = Math.pow(linear, 3);
              ctx.globalAlpha = Math.min(1, enhanced) * fade * 0.6;
              const scale = 0.35 + 0.65 * fade;
              ctx.fillStyle = starFillStyle;
              const drawSize = s.size * scale;
              ctx.fillRect(tp.x - drawSize / 2, tp.y - drawSize / 2, drawSize, drawSize);
            }
          }
          ctx.globalAlpha = Math.min(1, enhanced * 1.02);
          ctx.fillStyle = starFillStyle;
          const headLeft = s.x - s.size * 0.1;
          const headTop = s.y - s.size * 0.1;
          drawCurvedBottomRect(headLeft, headTop, s.size * 1.2, s.speed);
          // Overbright additive pass for alpha >1
          if (enhanced > 1.02) {
            const over = Math.min(1, (enhanced - 1) * 0.85); // compress tail
            ctx.save();
            ctx.globalCompositeOperation = 'lighter';
            ctx.globalAlpha = over;
            ctx.fillStyle = starFillStyle;
            const glowSize = s.size * 0.75;
            drawCurvedBottomRect(headLeft + s.size * 0.225, headTop + s.size * 0.225, glowSize, s.speed);
            ctx.restore();
          }
        } else {
          ctx.globalAlpha = Math.min(1, baseAlpha);
          ctx.fillStyle = starFillStyle;
          drawCurvedBottomRect(s.x, s.y, s.size, s.speed);
          if (baseAlpha > 1.01) {
            const over = Math.min(0.9, (baseAlpha - 1) * 0.8);
            ctx.save();
            ctx.globalCompositeOperation = 'lighter';
            ctx.globalAlpha = over;
            ctx.fillStyle = starFillStyle;
            const glowSize = s.size * 0.55;
            drawCurvedBottomRect(s.x + s.size * 0.225, s.y + s.size * 0.225, glowSize, s.speed);
            ctx.restore();
          }
        }
      }
      ctx.globalAlpha = 1;
      requestAnimationFrame(render);
    };
    render();
    return () => window.removeEventListener('resize', resize);
  }, []);

  return (
    <canvas
      ref={canvasRef}
      data-darkreader-ignore
      className="fixed inset-0 z-0 bg-black pointer-events-none [isolation:isolate]"
    />
  );
}
